import React, { Component } from 'react';
import logo from '../../logo.svg';
import '../../css/styles.css';
import icons from '../../vendors/feather/feather-sprite.svg';
import {
  Route,
  Link,
  NavLink,
  Switch,
  HashRouter
} from "react-router-dom";
import Photography from "./Photography";
import Graphics from "./Graphics";
import Colors from "./Colors";
import Textures from './Textures';
import Typography from './Typography';
import UxUi from './UX-UI';
import Main from "./Main";
import createHashHistory from 'history/createHashHistory';
const hashHistory = createHashHistory({ basename: process.env.PUBLIC_URL });


class Sidebar extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className='sidebar'>
        <nav className='sidebar__nav'>
          <ul className='sidebar__list'>
            <li className='sidebar__item'><NavLink className="sidebar__link" to='/photography'>Photography</NavLink></li>
            <li className='sidebar__item'><NavLink className="sidebar__link" to='/graphics'>Graphics</NavLink></li>
            <li className='sidebar__item'><NavLink className="sidebar__link" to='/textures'>Textures</NavLink></li>
            <li className='sidebar__item'><NavLink className="sidebar__link" to='/typography'>Typography</NavLink></li>
            <li className='sidebar__item'><NavLink className="sidebar__link" to='/colors'>Colors</NavLink></li>
            <li className='sidebar__item'><NavLink className="sidebar__link" to='/ux-ui'>UX/UI</NavLink></li>
          </ul>
        </nav>
      </div>
    );
  }
}

const Home = () => (
  <section className='section section--home'>
    <NavGrid />
  </section>
);

const Logo = (props) => {
  return (
    <div className={`app-logo app-logo--${props.size}`}>
    <Link className="app-logo__link" to='/'>
    <svg className='feather feather-bookmark'
    xmlns= "http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    viewBox='0 0 24 24'
    width='24'
    height='24'
    fill="none"
    stroke="currentColor"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round">
      <use xlinkHref={`${icons}#bookmark`} />
    </svg>
    <span className="app-name">Makerbook</span></Link>
    </div>
  );
}

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      navIsToggled: false
    };
    this.handleToggleClick = this.handleToggleClick.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

    handleToggleClick() {
      this.setState(state => ({
        navIsToggled: !state.navIsToggled
      }));
    }

    handleClick(e) {
      this.setState(state => ({
        navIsToggled: false
      }));
      console.log(this.setState);
    }

  render() {
    return (
      <header className={`header${this.state.navIsToggled ? ' show-nav' : ''}`}>
        <button onClick={this.handleToggleClick} className="sidebar__btn--toggle"><SidebarIcon name="menu" /></button>
        <h1 onClick={this.handleClick} className="header__title"><Logo size={this.props.logoSize} /></h1>
        <Sidebar />
      </header>
    );
  }
}

function SidebarIcon(props) {
  return (
    <svg className={`sidebar__icon feather feather-${props.name}`}
    xmlns= "http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    viewBox='0 0 24 24'
    width="24"
    height="24"
    fill="none"
    stroke="currentColor"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round">
      <use xlinkHref={`${icons}#${props.name}`} />
    </svg>
  );
}



function TileIcon(props) {
  return (
    <svg className={`tile__icon feather feather-${props.name}`}
    xmlns= "http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    viewBox='0 0 24 24'
    width="24"
    height="24"
    fill="none"
    stroke="currentColor"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round">
      <use xlinkHref={`${icons}#${props.name}`} />
    </svg>
  );
}

class NavTile extends Component {
  constructor(props) {
    super(props);
  }
  
  render() {
    return (
      <div className='tile nav__tile'>
        <Link className='tile__link' to={this.props.tileURL}>
          <TileIcon className="tile__icon" name={this.props.tileIcon} />
          <span className='tile__name'>{this.props.tileName}</span>
        </Link>
      </div>
    );
  }
}

class NavGrid extends Component {
  constructor(props) {
    super(props);
  }

  
  render() {
    return (
      <div className ="grid">
      <NavTile tileName='Photography' tileURL='/photography' tileIcon='camera' />
      <NavTile tileName='Graphics' tileURL='/graphics' tileIcon='image' />
      <NavTile tileName='Textures' tileURL='/textures' tileIcon='grid' />
      <NavTile tileName='Typography' tileURL='/typography' tileIcon='type' />
      <NavTile tileName='Colors' tileURL='/colors' tileIcon='droplet' />
      <NavTile tileName='UX/UI' tileURL='/ux-ui' tileIcon='user' />
        </div>
    )
  }
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isHomePage: true
    };
  }

  handlePageChange(val) {
    this.setState({isHomePage: val});
  }
  render() {
    const {location} = this.props;

    return (
      <HashRouter history={hashHistory}>
      <div className='app'>
      <p>{this.props.pathname}</p>
        <Header logoSize={this.state.isHomePage ? 'lg' : 'sm'} />
        <Switch location ={location}>
          <Route exact path="/" component={Home} />
          <Route path="/photography" component={Photography} />
          <Route path="/graphics" component={Graphics} />
          <Route path="/colors" component={Colors} />
          <Route path="/textures" component={Textures} />
          <Route path="/typography" component={Typography} />
          <Route path="/ux-ui" component={UxUi} />
        </Switch>
        </div>
      </HashRouter>
      )
  }
}

export default App;




